<template>
	<div id="header-nav">
		<!-- v-forkey -->
		<router-link v-for="(item,index) in navList" :key="index" v-bind:to="item.href"> {{ item.name }} </router-link>
	</div>
</template>
 
<script>
export default {
	name: 'headerNav',
	data(){
		return {
			navList: [
				{ name: '', href: '/index' },
				{ name: '', href: '/today-plan' },
				{ name: '', href: '/study-progress' },
				{ name: '', href: '/my-project' },
				{ name: '', href: '/my-blog' },
				{ name: '', href: '/summary' },
				{ name: '', href: '/technology' },
				{ name: '', href: '/used-website' },
				{ name: '', href: '/picture-enjoy' }
			]
		}
	}
}
</script>
 
<style>
#header-nav a{
	color: #fff;
	margin: 0 5px
}
#header-nav a:hover{
	color: #d8dce5
}
</style>


